import React, {useMemo, useState} from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import {LabelInput} from "../widget/label_input";
import ethImg from "../../../assets/images/pool_eth.png";
import cloneImg from "../../../assets/images/colse_img.png"

import {useForm} from "react-hook-form";
import {useSendModal} from "../../../widget/modal/send_modal";
import {NftPoolProps} from "../index";
import {LoadingBtn} from "../../../compones/loading_btn";
import {useMulticall, useWithdrawETH} from "../../../server/pair";
import {ModalProps} from "../../../provider/model_provider";
import {CURVE} from "../../../web3/token.config";
import {useDepositETH} from "../../../server/factory";
import {useWallet} from "use-wallet";
import BigNumber from "bignumber.js";
import {getBalanceNumber} from "../../../utils/formatBalance";

type DepositModel = {} & NftPoolProps & ModalProps

export const TokenDepositModel: React.FC<DepositModel> = ({sudo, onDismiss}) => {

    const {register, errors, getValues, setValue} = useForm();
    const handleDepositETH = useDepositETH(sudo?.id || '')
    const {balance} = useWallet()
    const {onSend, status} = useSendModal(handleDepositETH)
    let _balance = useMemo(() => {
        return new BigNumber(balance).div(1e18)
    }, [balance])
    return (
        <WapStyle>
            <div className="model_title">
                Deposit ETH
                {
                    onDismiss && <img src={cloneImg} onClick={() => onDismiss()} alt=""/>
                }
            </div>
            <CellBottomStyle container>
                <div className="pt">
                    <LabelInput errors={errors}
                                inputRef={register({})}
                                lastTis={''}
                                label={`Avaliable: ${getBalanceNumber(balance)}`}
                                name={'price'}
                                placeholder={""}
                    >
                        <div className="right_btn">

                            <p onClick={() => {
                                setValue('price', _balance)
                            }}>max</p>
                            <img className={'ethImg'} src={ethImg} alt=""/>
                            <span>ETH</span>
                        </div>
                    </LabelInput>
                </div>
            </CellBottomStyle>

            <div className={'bottom'}>
                <LoadingBtn isLoading={status} onClick={() => {
                    let {price} = getValues();
                    onSend(price)
                }}>
                    <h4>Deposit ETH</h4>
                </LoadingBtn>
            </div>
        </WapStyle>
    )
}


const WapStyle = styled.div`

  width: 60%;
  margin: 0 auto;
  background-color: #191b1f;
  border-radius: 20px;
  border: 1px solid #2C2F36;
  padding: 10px 20px;
  box-sizing: border-box;

  .bottom {
    display: flex;
    justify-content: center;

    > div {
      width: 30%;
      background: #FFFFFF;
      color: #2C2F36;
      border: 2px solid #D9D9D9;
      margin-top: 0;

      h4 {
        font-size: 14px;
      }
    }
  }

  .model_title {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-align: center;
    display: flex;
    align-items: center;
    color: #D9D9D9;
    border: 1px solid #191B1F;
    position: relative;
    justify-content: center;

    img {
      display: block;
      width: 20px;
      position: absolute;
      cursor: pointer;

      right: 0;
    }
  }
`


